<template>
  <div class="toBePaid">
    <van-nav-bar
      title="购物车"
      left-arrow
      @click-left="$router.back(-1)"/>

    <div class="distribution-mode">
      <span>配送方式</span>
      <van-button size="small" type="danger" >商家配送</van-button>
      <van-button size="small" :disabled="true">到店自取</van-button>
    </div>

    <div class="address-bg"></div>
    <div class="address" @click="goEditContacts">
      <div>
        <img src="../assets/img/address.png">
        <span>请填写收货地址</span>
      </div>
      <van-icon name="arrow" />
    </div>

    <van-card
      v-for="item in 2"
      num="2"
      price="2.00"
      desc="描述信息"
      title="商品标题"
      :thumb="imageURL"
    />

  <div class="select-date">
    <van-cell title="配送日期" is-link @click="setDate=true" />
    <van-cell title="配送时间" is-link  @click="setTime=true"/>
  </div>

    <van-popup v-model="setDate" position="bottom" :overlay="true">
      <van-datetime-picker
        v-if="setDate"
        v-model="currentDate"
        type="date"
        :min-date="currentDate"
        @confirm="confirmBtn"
        @cancel="cancelBtn"/>
    </van-popup>

    <van-popup v-model="setTime" position="bottom" :overlay="true">
      <van-datetime-picker
        v-if="setTime"
        v-model="currentTime"
        type="time"
        @confirm="confirmTime"
        @cancel="cancelTime"/>
    </van-popup>

    <div class="pay-mode">
      <div class="pay-mode-title">支付方式</div>
      <van-radio-group v-model="radio">
        <van-cell-group>
          <van-cell  clickable @click="radio = '1'">
            <template slot="title">
              <div style="display: flex;align-items: center">
                <van-icon slot="right-icon" name="wechat" class="custom-icon"  color="#09BB07" size="1.5rem"/>
                <span class="custom-text">微信支付</span>
              </div>
            </template>
            <van-radio name="1" checked-color="#07c160"/>
          </van-cell>
        </van-cell-group>
      </van-radio-group>


    </div>

    <div class="remarks" style="font-size: 14px;">
      <div class="price">
        商品合计
        <span style="color:#f44">￥198</span>
      </div>
      <van-cell-group>
        <van-field
          v-model="message"
          label="备注"
          type="textarea"
          placeholder="请输入备注"
          rows="1"
          autosize/>
      </van-cell-group>
    </div>

    <van-submit-bar
      :price="19800"
      button-text="去结算"
      @submit="onSubmit"
    />
  </div>
</template>

<script>
  export default {
    data(){
      return {
        imageURL:'https://img.yzcdn.cn/2.jpg',
        setDate:false,
        setTime:false,
        currentDate: new Date(),
        currentTime: '12:00',
        message:'',
        radio:''

      }
    },
    methods: {
      formatter(type, value) {
        if (type === 'year') {
          return `${value}年`;
        } else if (type === 'month') {
          return `${value}月`
        }
        return value;
      },
      confirmBtn(){
        this.setDate=false
      },
      cancelBtn(){
        this.setDate=false
      },
      confirmTime(){
        this.setTime=false
      },
      cancelTime(){
        this.setTime=false
      },
      onSubmit(){
        this.$dialog.confirm({
          title: '提示',
          message: '确认提交订单？'
        }).then(() => {
          this.$router.push({path:'./myPage'})
        }).catch(() => {
          // on cancel
        });
      },
      goEditContacts(){
        this.$router.push({path:'./contacts'})
      }
    }
  }
</script>

<style lang="scss" scoped>
    .toBePaid{
      background: #F1F1F1;
      .remarks{
        margin-top: 0.5rem;
        background: #fff;
        .price{
          text-align: left;
          padding: 0.8rem 0.8rem;
          border-bottom: 1px solid #F1F1F1;
          span{
            float: right;
          }
        }
      }
      .pay-mode-title{
        text-align: left;
        padding: 0.5rem  0.8rem;
        font-size: 0.8rem;
      }
      .select-date{
        margin: 0.5rem 0;
      }
      .van-card{
        background: #fff !important;
        border-bottom: 0.1rem solid #F1F1F1;
        margin-top: 0;
      }
      .address-bg{
        width: 100%;
        height: 0.2rem;
        background: linear-gradient(45deg,#1989FA 12.5%, #fff 0,
          #fff 25%, #FF6C6C 0,
          #FF6C6C 37.5%, #fff 0,
          #fff 50%, #1989FA 0,
          #1989FA 62.5%, #fff 0,
          #fff 75%, #FF6C6C 0,
          #FF6C6C 87.5%, #fff 0);
        background-size: 120px 120px;
      }
      .address{
        text-align: left;
        background: #fff;
        padding: 1rem 0.8rem;
        font-size: 20px;
        display: flex;
        align-items: center;
        margin-bottom: 0.5rem;
        div{
          display: flex;
          align-items: center;
          flex: 1;
        }
        img {
          width: 1rem;
          height: 1rem;
          margin-right: 1rem;
        }

      }
      .distribution-mode{
        text-align: left;
        background: #fff;
        padding: 0.5rem 0.8rem;
        margin-bottom: 0.5rem;
        font-size: 20px;
        span{
          margin-right: 0.5rem;
        }
      }
    }
</style>



